<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  created(){
    if (sessionStorage.getItem("store") ) {
      this.$store.replaceState(Object.assign({}, this.$store.state,JSON.parse(sessionStorage.getItem("store"))))
    }

    //在頁面重新整理時將vuex裡的資訊儲存到sessionStorage裡
    window.addEventListener("beforeunload",()=>{
      sessionStorage.setItem("store",JSON.stringify(this.$store.state))
    })

    const options = {
      headers: {'Content-Type': 'application/json'},
      withCredentials: true
    };

    // this.$cookies.set('cpod.sid', 's%3AT1QSpaeW4yQGBHbBmb96eFMZYo9E9_nV.i64QZSo2oS6O8TqVQKjHVew%2Fntv%2BdW6Z%2Fk9Cz3U3Bls');

    //GET TOKEN FROM API
    let api = this.$store.state.API.getToken.url;

    let param = this.$store.state.API.getToken.parameter;

    let self = this
    if(self.$http.defaults.headers.common['Authorization'] == "bearer " || self.$http.defaults.headers.common['Authorization'] == "" ){
      this.$http.post(api, param, options)
      .then((response) => {
        if(response.data){
          console.log("Get token:"+JSON.stringify(response.data));
          self.$store.state.Authorization.bearerToken = "bearer " + response.data.token;
          const token = self.$store.state.Authorization.bearerToken
          self.$store.state.Authorization.userId = response.data.userId
          self.$http.defaults.headers.common['Authorization'] = token
          //this.lessonTitle = '"'+response.data.title+'"'
        }
      }).catch((error) => {
        console.log(error)
        window.location.href = '/login';
      });
    }




  }
}
</script>


<style lang="scss">
@import "~bootstrap/scss/bootstrap"


</style>

<style>
body {
  font-family: Lato;
}
</style>




