import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    currentLesson : "",
    // userRemindData: {},
    slidesNumber: 0,
    uploadCompleted: false,
    userFiles:{},
    API: {
      submitButtonDisabled: false,
      getToken: {
        url: "https://staging.chinesepod.com/api/v1/token", 
        parameter: {
          "key": "aaaa1111bbbb2222cccc3333",
          "type": "testing"
        }
      },
      getLessonInfo: {
        url: "https://staging.chinesepod.com/api/v1/lessons/get-lesson/"
      },
      getLessonUploadedContent:{
        url: "https://staging.chinesepod.com/api/v1/recap/get-content/"
      }
    },
    Authorization:{
      bearerToken: "",
      userId: ""
    }
  }
});

export default store;


