<template>
  <div>
    <p class="mt-5">Submission for Lesson {{lessonNumber}}</p>
    <p class="mt-1">{{ lessonTitle }}</p>
    <p class="mb-4 mt-1">{{remindMsg}}</p>
    <slot :files="files" :file-list="fileList" :started="started">
      <uploader-unsupport></uploader-unsupport>
      <uploader-drop @ondragenter="document.getElementsByClassName('overlay').toggle('overlay_show')">
      <div class="uploadContainer mt-4">
        <i class="fas fa-cloud-upload-alt uploadIcon"></i>
        <div class="uploadPageDescription">
          <div>Please Drop your Files here</div>
          <!-- <div>OR</div>
          <div class="filesAndFolderText">
            <uploader-btn><a>File</a></uploader-btn> / 
            <uploader-btn :directory="true"><a>Folder</a></uploader-btn>
          </div> -->
        </div>
      </div>
        <div class="overlay"></div>
      </uploader-drop>

      <uploader-list></uploader-list>
      <div class="submitBtn mt-5">
         <button type="submit" v-if="submitEnable" class="btn btn-secondary" @click="submitFiles" :disabled="buttonDisabled">Submit</button>
         <loading :active.sync="isLoading"></loading>
      </div>
      <div class="uploadedList">
        <span>{{ MissingFileTitle }}</span>
        <span v-bind:key="mfile" v-for="mfile in MissingFiles">{{ mfile }}</span>
        <span>{{ uploadedListTitle }}</span> 
        <span v-bind:key="file" v-for="file in userFiles">{{ file }}</span>
      </div>
    </slot>

    <!-- Modal -->
    <div class="modal fade" id="errorModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <i class="fas fa-exclamation-triangle errorIcon"></i>
            <h5 class="modal-title ml-2" id="exampleModalLabel">Error</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="verifyErros">
              <span v-bind:key="error" v-for="error in errorModalMsg">{{error}}</span>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary okBtn" data-dismiss="modal">OK</button>
            <!-- <button type="button" class="btn btn-primary">Save changes</button> -->
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  import $ from 'jquery'
  import Uploader from 'simple-uploader.js'
  import { kebabCase } from '../common/utils'
  import UploaderBtn from './btn.vue'
  import UploaderDrop from './drop.vue'
  import UploaderUnsupport from './unsupport.vue'
  import UploaderList from './list.vue'
  import UploaderFiles from './files.vue'
  import UploaderFile from './file.vue'

  const COMPONENT_NAME = 'uploader'
  const FILE_ADDED_EVENT = 'fileAdded'
  const FILES_ADDED_EVENT = 'filesAdded'
  const UPLOAD_START_EVENT = 'uploadStart'

  export default {
    name: COMPONENT_NAME,
    provide () {
      return {
        uploader: this
      }
    },
    props: {
      options: {
        type: Object,
        default () {
          return {}
        }
      },
      autoStart: {
        type: Boolean,
        default: true
      
      },
      fileStatusText: {
        type: [Object, Function],
        default () {
          return {
            success: 'success',
            error: 'error',
            uploading: 'uploading',
            paused: 'paused',
            waiting: 'waiting'
          }
        }
      }
    },
    data () {
      return {
        started: false,
        files: [],
        fileList: [],
        lessonNumber: '',
        verifyErros: [],
        submitEnable: false,
        remindMsg: '',
        slidesNumber: 0,
        allUploadFinished: false,
        userFiles: [],
        uploadedListTitle: "",
        lessonTitle: "",
        MissingFiles: [],
        MissingFileTitle: "",
        isLoading: false,
        errorModalMsg: []
      }
    },
    methods: {
      splitFileName(filename){
        let splitted = filename.split(".");
        let numGroup = splitted.length - 1;
        let extension = splitted[numGroup];
        return {
          splitted : splitted,
          numGroup : numGroup,
          extension : extension
        }
      },
      verifyFile (filename) {
        let result = this.splitFileName(filename);
        return this.verifyUploadName(result.splitted,result.numGroup,result.extension,this.lessonNumber);
      },
      uploadStart () {
        this.started = true;
        
      },
      fileAdded (file) {
        this.$store.state.submitButtonDisabled = true;
        let verifyResult = this.verifyFile(file.name);
        if(verifyResult){
          this.verifyErros.push("(" + file.name + ") " + verifyResult + " ; ");
          file.ignored = true
        }else{
          let fileLength = (this.fileList.length <= 0 ? 1 : this.fileList.length);
          this.updateSubmitEnable(fileLength);
        }
        this.$emit(kebabCase(FILE_ADDED_EVENT), file)
        if (file.ignored) {
          // is ignored, filter it
          return false
        }
      },
      filesAdded (files, fileList) {
        if(this.verifyErros.length > 0) $('#errorModal').modal('show');
        var self = this;
         $('#errorModal').on('hidden.bs.modal', function () {
            self.verifyErros = [];
          })
        this.$emit(kebabCase(FILES_ADDED_EVENT), files, fileList)
        if (files.ignored || fileList.ignored) {
          // is ignored, filter it
          return false
        }
      },
      fileRemoved (file) {
        this.updateSubmitEnable(this.fileList.length);
        this.files = this.uploader.files
        this.fileList = this.uploader.fileList
      },
      filesSubmitted (files, fileList) {
        this.files = this.uploader.files
        this.fileList = this.uploader.fileList
        if (this.autoStart) {
          this.uploader.upload()
        }
      },
      allEvent (...args) {
        const name = args[0]
        const EVENTSMAP = {
          [FILE_ADDED_EVENT]: true,
          [FILES_ADDED_EVENT]: true,
          [UPLOAD_START_EVENT]: 'uploadStart'
        }
        const handler = EVENTSMAP[name]
        if (handler) {
          if (handler === true) {
            return
          }
          this[handler].apply(this, args.slice(1))
        }
        args[0] = kebabCase(name)
        this.$emit.apply(this, args)
      },
      verifyUploadName(Group,numGroup,extension,currentLesson){
          let error = "";
          if(extension != "jpg" && extension != "wav" && extension != "mp3") return "The file type is not valid, accept jpg, wav, mp3.";
          if(numGroup<2) return "The file name is not valid.";
          if(Group[0] != currentLesson) return ("The file name does not correspond to lesson " + currentLesson) + '.';
          if(extension == "jpg") {
              if(Group[1] == "background"){
                  if(numGroup != 3)  return ("Background image file name does not comply with spec, use " + Group[0] + "background.[sp|tl|tp|sl]" + extension+ ".");
                  if(Group[2] != "sl" && Group[2] != "sp" && Group[2] != "tl" && Group[2] != "tp")  return "Error: Category must be one of the following: sp, sl, tp, tl, example: 4123.3.0032.sl.";
              }else{
                  if(numGroup == 4 && (Group[3] != "sl" && Group[3] != "sp" && Group[3] != "tl" && Group[3] != "tp"))  return "Error: Category must be one of the following: sp, sl, tp, tl, example: 4123.3.0032.sl.";
                  
                  if(numGroup == 4){
                    if(Group[2].length != 4 || isNaN(Group[2]))  return "Error: Length must be 4 numeric digits MMSS.";
                    if(!(/^\d+$/.test(Group[2])) && (Group[2][1] > 5 || Group[2][3] > 5))  return "Error: Invalid Offset - must be 4 digits from 0-9 in format MMSS (MM=Minutes, SS=Seconds - example 0032 for 0 Minutes, 32 seconds.";
                  }else{
                    if(numGroup != 3)  return ("Image file name does not comply with spec, use " + Group[0] + "." + extension+ ".");
                    if(Group[1].length != 4 || isNaN(Group[1]))  return "Error: Length must be 4 numeric digits MMSS.";
                    if(Group[1] != "0000"  )   return ("Image file name does not comply with spec, use " + Group[1] + "." + extension+ ".");
                  }
              }
          }
          if(extension == "wav" || extension == "mp3") {    
              if(numGroup != 2 && numGroup != 3)  return ("Audio file name does not comply with spec, use " + Group[0] + "." + extension+ ".");
              if(numGroup == 3 && Group[2] != "0000"  )   return ("Audio file name does not comply with spec, use " + Group[1] + "." + extension+ ".");   
          }
      },
      updateSubmitEnable(fileNumber) {
        if(fileNumber>0){
          this.submitEnable = true;
        } else {
          this.submitEnable = false;
        }
      },
      submitFiles(){
        this.isLoading = true;
        let files = this.fileList.valueOf();
        let Errors = this.filesIntegrityTest(files);
        this.errorModalMsg = Errors;

        if(Errors && Errors.length != 0){
          let self = this;
          setTimeout(function(){
            $('#errorModal').modal('show');        
            self.isLoading = false;
          }, 1000)

        }else{
          let self = this;
          setTimeout(function(){
            self.$router.push("/submission-received/");         
          }, 1000)
        }
      },
      filesIntegrityTest(files){
        console.log("fileIntegrityTest : " + files);
        let checkResult = {};
        for(let i = 0; i < files.length; i++){
          let result = this.splitFileName(files[i].name);
          if(result.numGroup == 4 || (result.numGroup == 3 && (result.extension.toUpperCase() == "MP3" || result.extension.toUpperCase() == "WAV"))){
            let varT = result.splitted[1];
            let varMMSS = result.splitted[2];
            let varType = result.splitted[3].toUpperCase();
            if( !(varT in checkResult)) checkResult[varT]= {};
            if( !(varMMSS in checkResult[varT]) ) checkResult[varT][varMMSS] = [];
            checkResult[varT][varMMSS].push(varType);
          }
        }
        return this.checkIntegrityResult(checkResult);
      },
      checkIntegrityResult(result){
        const varT = Object.keys(result)
        let errorText = "";
        let totalErrorText = [];
          for (const T of varT) {
            const varMMSS = Object.keys(result[T])
            if(!result[T]["0000"] || !(result[T]["0000"].includes("WAV") || result[T]["0000"].includes("MP3"))){
              // totalErrorText.push("No " + this.lessonNumber + "." + T + ".0000.wav present. ; ");
              totalErrorText.push("No " + this.lessonNumber + "." + T + ".0000.wav(mp3) present.");
            }
            for (const MMSS of varMMSS){
                if(result[T][MMSS].length == 1 && (result[T][MMSS].includes("WAV") || result[T][MMSS].includes("MP3"))) continue;
                this.slidesNumber += result[T][MMSS].length;
                let needFile = [];
                //console.log("T"+T+"MMSS"+MMSS)
                if(!(result[T][MMSS].includes("SP"))) needFile.push(this.lessonNumber + "." + T + "." + MMSS + ".sp.jpg")
                if(!(result[T][MMSS].includes("SL"))) needFile.push(this.lessonNumber + "." + T + "." + MMSS + ".sl.jpg")
                if(!(result[T][MMSS].includes("TP"))) needFile.push(this.lessonNumber + "." + T + "." + MMSS + ".tp.jpg")
                if(!(result[T][MMSS].includes("TL"))) needFile.push(this.lessonNumber + "." + T + "." + MMSS + ".tl.jpg")
                errorText = "Please also transmit the files ";
                for (let i = 0; i < needFile.length; i++){
                  if (needFile.length >= 2 && i == needFile.length -1){
                    errorText += " and " + needFile[i];
                  } else if( i == 0 ){
                    errorText += needFile[i];
                  } else {
                    errorText += ", " + needFile[i];
                  }
                }
                errorText += ". ; "  
                if(errorText != "Please also transmit the files . ; ") totalErrorText.push(errorText);
            }  
          }
          this.$store.state.slidesNumber = this.slidesNumber;
          return totalErrorText;

      },
      getSubmissionSurface(userFiles){

        // let userFiles = this.$store.state.userFiles;

        let slide = 0;
        let audio = 0;
        let fileList = this.processListToFiles(userFiles);
        //console.log(fileList)
        let result = this.evaluateUserFiles(fileList);
        //console.log(result)
        
        if(result == "CONTENT_OK") {
          for(var i = 0; i < userFiles.length; i++){
            let findFileType = this.splitFileName(userFiles[i]).extension;

            if(findFileType == "jpg"){
              slide = slide + 1;
            }else{
              audio = audio + 1;
            }
          }
          this.remindMsg = "Lesson "+ this.lessonNumber +" already has content: One Audio Recap Length "+ audio +" with "+ slide +" slides (each in 4 versions) But you can upload additional files below.";
        }else if (result == "NO_CONTENT_YET"){
          this.remindMsg = "No content exists presently for "+ this.lessonNumber +". Please submit your files below:"
        }else if (result == "CONTENT_INCOMPLETE"){
          this.remindMsg = "Uploaded content for "+ this.lessonNumber +" is incomplete. Please see errors below submission box, and upload missing/corrected files."
        }

      },
      processListToFiles(fileList){
        console.log("processListToFiles");
        let finalList = []
        for(let i=0; i < fileList.length; i++){
          var filename = fileList[i].substring(fileList[i].lastIndexOf('/')+1);
          let file = {};
          file.name = filename;
          finalList.push(file);
        }
        return finalList
        },
      evaluateUserFiles(userFiles){
        console.log("evaluateUserFiles");
        if(userFiles.length <= 0){
          return "NO_CONTENT_YET"
        }else{
          let errors = this.filesIntegrityTest(userFiles);
          if(errors.length>0){
            this.MissingFileTitle = "Missing Files:";
            this.uploadedListTitle = "Existing Assets:";
            this.MissingFiles = errors;
            return "CONTENT_INCOMPLETE"
          }else{
            this.uploadedListTitle = "Existing Assets:";
            return "CONTENT_OK"
          }

        }
      },
      getLessonInfo(){
        let api = this.$store.state.API.getLessonInfo.url + this.lessonNumber;
        this.$http.get(api)
          .then((response) => {
            if(response.data){    
              //console.log(response.data);  
              this.lessonTitle = '"'+response.data.title+'"'
            }
          })

      }
    },
    created () {
      this.options.initialPaused = !this.autoStart
      const uploader = new Uploader(this.options)
      this.uploader = uploader
      this.uploader.fileStatusText = this.fileStatusText
      uploader.on('catchAll', this.allEvent)
      uploader.on(FILE_ADDED_EVENT, this.fileAdded)
      uploader.on(FILES_ADDED_EVENT, this.filesAdded)
      uploader.on('fileRemoved', this.fileRemoved)
      uploader.on('filesSubmitted', this.filesSubmitted)

      this.lessonNumber = this.$route.params.id
      const api = this.$store.state.API.getLessonUploadedContent.url + this.lessonNumber;
      this.$http.get(api)
        .then((response) => {
          if(response.data){       
            this.userFiles = response.data;  
            this.getSubmissionSurface(this.userFiles)
            this.getLessonInfo()
          }
        }).catch((error) => {
                  console.log(error)
                  this.$router.push("/"); 
        });

        if (sessionStorage.getItem("store") ) {
          this.$store.replaceState(Object.assign({}, this.$store.state,JSON.parse(sessionStorage.getItem("store"))))
          }

          window.addEventListener("beforeunload",()=>{
            sessionStorage.setItem("store",JSON.stringify(this.$store.state))
        })

    },
    destroyed () {
      const uploader = this.uploader
      uploader.off('catchAll', this.allEvent)
      uploader.off(FILE_ADDED_EVENT, this.fileAdded)
      uploader.off(FILES_ADDED_EVENT, this.filesAdded)
      uploader.off('fileRemoved', this.fileRemoved)
      uploader.off('filesSubmitted', this.filesSubmitted)
      this.uploader = null
    },
    computed: {
      buttonDisabled(){
        return this.$store.state.submitButtonDisabled;
      }
    },
    components: {
      UploaderBtn,
      UploaderDrop,
      UploaderUnsupport,
      UploaderList,
      UploaderFiles,
      UploaderFile
    }
  }
</script>

<style>
  div,p {
    font-size: 14px;
  }
  .uploader {
    position: relative;
  }
  .submitBtn{
    text-align: center;
  }
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    background-size: 30px 30px;
    background: linear-gradient(360deg, rgba(36, 155, 227, 0.1)24%, rgba(211, 216, 218, 0.1)76%);
    background: -moz-linear-gradient(360deg, rgba(36, 155, 227, 0.1)24%, rgba(211, 216, 218, 0.1)76%);
    background: -webkit-linear-gradient(360deg, rgba(36, 155, 227, 0.1)24%, rgba(211, 216, 218, 0.1)76%);
    background: -o-linear-gradient(360deg, rgba(36, 155, 227, 0.1)24%, rgba(211, 216, 218, 0.1)76%);
    z-index: 1;
  }
  @keyframes barberpole {
    from { background-position: 0 0; }
    to   { background-position: 60px 30px; }
  }

  .uploadPageDescription {
    line-height: 45PX;
    color: #9E9E9E;
  }
  .uploadContainer {
    /* margin: auto; */
    /* width: 100%; */
    text-align: center;
  }
  .uploadIcon, .errorIcon {
    margin: auto 0px;
  }
  .uploadIcon {
    font-size: 90px;
    color: #E0E0E0
  }
  .errorIcon {
    font-size: 14px;
    color: #172664;
  }
  .filesAndFolderText {
    color: #0097f0;
  }
  .modal-footer {
    border: 0px solid #dee2e6; 
  }
  .modal-title {
    margin: auto;
    font-weight: normal;
    color: #172664;
    font-size: 14px;
  }
  .modal-header {
    background-color: #f5f5f5;
    height: 40px;
    padding: 10px;
  }
  .modal-body {
    font-size: 14px;
    color: #172664;
    padding: 36px;
    padding-bottom: 24px;
  }
  .okBtn {
    height: 32px;
    padding: 7px 16px;
    font-size: 12px;
    background-color: #ef6868;
    border: none;
  }
  .okBtn:hover {
    background-color: #f44336;
  }
  .modal-header .close {
    color: #fff;
    margin: -1rem -10px -1rem auto;
  }
  .modal-header .close:hover {
    color: #FFEBEE
  }
  .modal-dialog {
    margin-top: 120px;
  }
  .verifyErros {
    color:#ef6868;
  }
  .verifyErros span ,.uploadedList span{
    float: left;
    clear: both;
    font-size: small;
  }
</style>



